<script lang='ts'>
	import { createEventDispatcher } from 'svelte';
	import { writable } from 'svelte/store';
	import {
		CSSUtility,
	} from '../../../resources/utilities';
	import type { CSS } from '../../../resources/utilities';
	import Button from '../Button.svelte';

	export let isStroke = false;
	export let isStrokeW = writable(isStroke);
	export let icon = 'done';
	export let iconW = writable(icon);
	export let twoToneFilter: CSS = '--filter-text-secondary';
	export let twoToneFilterW = writable(twoToneFilter);
	export let fillColour: CSS = '--colour-text-secondary';
	export let fillColourW = writable(fillColour);

	const dispatch = createEventDispatcher();
</script>

<Button
	on:click={() => dispatch('click')}
	on:mouseover={() => dispatch('mouseover')}
	on:mousein={() => dispatch('mousein')}
	on:mouseout={() => dispatch('mouseout')}
	{...$$restProps}
>
	{#if $isStrokeW} 
		<icon
			style='
				--filter-twotone: {CSSUtility.parse($twoToneFilterW)};
			'
		>
			{$iconW}
		</icon>
	{:else}
		<iconf
			style='
				--colour-fill: {CSSUtility.parse($fillColourW)};
			'
		>
			{$iconW}
		</iconf>
	{/if}
	<slot></slot>
</Button>

<style>
	icon {
		filter: var(--filter-twotone);
	}

	iconf {
		color: var(--colour-fill);
	}
</style>